import React from 'react';
import { Card, Row, Col } from 'antd';
import { ExperimentOutlined, BookOutlined } from '@ant-design/icons';
import './Home.css';

const Home: React.FC = () => {
  return (
    <div className="home-container">
      <h1>Добро пожаловать в Interactive Physics</h1>
      <p className="intro-text">
        Изучайте физику с помощью интерактивных симуляций и экспериментов
      </p>
      
      <Row gutter={[16, 16]} className="features-section">
        <Col xs={24} sm={12}>
          <Card 
            title={
              <span>
                <ExperimentOutlined /> Эксперименты
              </span>
            }
          >
            Проводите виртуальные эксперименты и наблюдайте за физическими явлениями в реальном времени
          </Card>
        </Col>
        <Col xs={24} sm={12}>
          <Card 
            title={
              <span>
                <BookOutlined /> Обучение
              </span>
            }
          >
            Изучайте физические законы и явления с помощью интерактивных визуализаций
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Home; 